body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
    background-color: #a4dfda;
    margin: auto;
    margin-top: 0px;
    margin-bottom: 500px;
    width: 100%;
    height: 50px;
    align-items: center;
    position: fixed;
    
  }
header ul li{
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  text-decoration: none;
  width: 25%;
}
.questions {
    background-color: #a4dfda;
    margin: auto;
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
  
}
.questionitem{
    
    align-items: center;
    padding: 20px 20px 20px;
    width: 90%;
    margin-bottom: 5%;
    border-radius: 10px;
    font-family: roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    background-color: #ffffff;

}


.finish {
  margin-top: 30%;
  color: #daa4a4 !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 10px;
  border: 4px solid #daa4a4 !important;
  display: inline-block;
  transition: all 0.4s ease 0s;
  }
  .finish:hover {
  color: #ffffff !important;
  background: #80f63b;
  border-color: #80f63b !important;
  transition: all 0.4s ease 0s;
  }

  .popup {  
    border-radius: 20px;
    position: fixed;  
    width: 90%;  
    height: 70%;  
    margin: auto;  
    background-color: rgb(203, 203, 231);  
    opacity: 100%;
    text-align: center;
    align-items: center;
    font-family: roboto, sans-serif;
    font-weight: 100;
    font-size: 1.2em;
    padding: 1em;
  }  
  .start {
    margin-top: 5%;
    color: #daa4a4 !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    padding: 10px;
    border: 4px solid #daa4a4 !important;
    display: inline-block;
    transition: all 0.4s ease 0s;
    }
    .start:hover {
    color: #ffffff !important;
    background: #80f63b;
    border-color: #80f63b !important;
    transition: all 0.4s ease 0s;
    }
    input[type=text] {
      width: 20%;
      height: 5%;
      text-align: center;
      border: 0px solid red;
      border-radius: 4px;
    }